import React from "react"
import Typewriter from 'typewriter-effect'

import "../styles/terminal.scss"

const Terminal = () => {
  return (
    <div id="terminal" style={{ height: "40rem" }} className="p-5 mt-8">
      <div className="m-auto md:w-7/12 h-5/6 rounded-sm">
        <div className="bg-gray-500 rounded-tl rounded-tr">
          <div className="p-1 px-2">
            <span className="block text-center text-white font-bold">sinan@bekar:~</span>
          </div>
        </div>
        <div className="bg-black h-full rounded-b-lg">
          <div className="text-white p-6 text md:text-xl">
            <span>
              <Typewriter
                options={{
                  autoStart: true,
                }}
                onInit={(typewriter) => {
                  typewriter
                    .pasteString(terminalSb)
                    .pauseFor(300)
                    .changeDelay(60)
                    .typeString('sudo ')
                    .pauseFor(100)
                    .typeString('systemctl ')
                    .pauseFor(100)
                    .typeString('start ')
                    .pauseFor(100)
                    .typeString('portfolio')
                    .pasteString('<br>')
                    .pauseFor(1500)
                    .pasteString('<span class="text-red-500">An error occurred while starting portfolio.</span> <br>Portfolio page is a work in progress and will be available soon. ')
                    .pasteString('<br>' + terminalSb)
                    .start();
                }}
              />
            </span>
          </div>
        </div>
      </div>
    </div>
  )
}

const terminalSb = '<span class="t-green">sinan@bekar</span>:<span class="t-blue">~</span>$ '

export default Terminal

